<template>
    <div id="wrap">
      <button type="button" @click="request">请求</button>
      <section-title sec-title="订单"></section-title>
      <tabs :paths="paths"></tabs>
      <div>
        <router-view></router-view>
      </div>
    </div>
</template>

<script>
  import sectionTitle from '../others/section-title'
  import tabs from '../others/tabs'
  export default {
    components: {
      sectionTitle,
      tabs
    },
    name: 'orderform',
    data () {
      return {
        paths:[
          {
            title: '全部订单',
            path: '/orderform/all'
          },
          {
            title: '未完成',
            path: '/orderform/incomplete'
          },
          {
            title: '完成',
            path: '/orderform/complete'
          }
        ],
        aaa: {
          firstName: '123',
          lastName: '456'
        }
      }
    },
    methods: {
      request(){

        let params = new URLSearchParams()
        params.append('params1', 'value1')
        params.append('params2', 'value2')

        this.$http.post('/GreenHealthFarm/ProblemServlet', params)
          .then(function(res){
              console.log(res)
          })
          .catch(function(err){
              console.log(err)
          })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #wrap{
    position: relative;
    background-color: #fff;
    width: 1030px;
    height: 968px;
    margin: 22px 0 0 26px;
  }


</style>



